import React, { useState, Suspense, lazy } from "react";
import { useWorkspace } from "./state/WorkspaceContext";
import { Sidebar } from "./components/Sidebar";
import { Gallery } from "./components/Gallery";
import { LoadingSpinner } from "./components/LoadingSpinner";
import { ErrorBoundary } from "./components/ErrorBoundary";

// ✅ Lazy loading para melhor performance - usando componente unificado
const WhiteboardUnified = lazy(() => import("./components/WhiteboardUnified"));
const WhiteboardGallery = lazy(() => import("./components/WhiteboardGallery"));

export default function App() {
  const { selectedSection } = useWorkspace();
  const [selectedWhiteboardId, setSelectedWhiteboardId] = useState<string | null>(null);

  // ✅ Função para renderizar conteúdo baseado na seção
  const renderContent = () => {
    if (selectedSection === "Whiteboards") {
      return (
        <Suspense fallback={<LoadingSpinner message="Carregando Whiteboard..." />}>
          {selectedWhiteboardId ? (
            <WhiteboardUnified
              whiteboardId={selectedWhiteboardId}
              onBack={() => setSelectedWhiteboardId(null)}
            />
          ) : (
            <WhiteboardGallery onSelect={setSelectedWhiteboardId} />
          )}
        </Suspense>
      );
    }
    
    return <Gallery />;
  };

  return (
    <ErrorBoundary>
      <div className="flex h-screen bg-gray-100">
        <Sidebar />
        <main className="flex-1 overflow-hidden">
          {renderContent()}
        </main>
      </div>
    </ErrorBoundary>
  );
}