import React, { createContext, useContext, useState } from "react";

const sections = [
  "Colaboradores",
  "Projetos",
  "Equipes",
  "Whiteboards",
] as const;

export type Section = typeof sections[number];

export interface Collaborator {
  id: string;
  name: string; // Padronizando para inglês
  role: string; // Padronizando para inglês
  superior?: string;
  subordinados: string[];
  projects: string[]; // Padronizando para inglês
}

export interface Project {
  id: string;
  name: string; // Padronizando para inglês
  link: string;
  collaborators: string[]; // Padronizando para inglês
  status?: string; // Adicionando status que é usado no Gallery
}

// Adicionando interface Team que estava faltando
export interface Team {
  id: string;
  name: string;
  members?: string[];
}

// Adicionando interface Whiteboard
export interface Whiteboard {
  id: string;
  name: string;
  createdAt: Date;
  updatedAt: Date;
  nodes?: any[];
  edges?: any[];
}

type WorkspaceContextType = {
  selectedSection: Section;
  setSelectedSection: (s: Section) => void;
  sections: readonly Section[]; // readonly para imutabilidade
  collaborators: Collaborator[]; // Padronizando para inglês
  setCollaborators: React.Dispatch<React.SetStateAction<Collaborator[]>>;
  projects: Project[]; // Padronizando para inglês
  setProjects: React.Dispatch<React.SetStateAction<Project[]>>;
  teams: Team[]; // Adicionando teams que estava faltando
  setTeams: React.Dispatch<React.SetStateAction<Team[]>>;
  whiteboards: Whiteboard[]; // Adicionando whiteboards
  setWhiteboards: React.Dispatch<React.SetStateAction<Whiteboard[]>>;
};

const WorkspaceContext = createContext<WorkspaceContextType | undefined>(undefined);

export function WorkspaceProvider({ children }: { children: React.ReactNode }) {
  const [selectedSection, setSelectedSection] = useState<Section>("Colaboradores");
  
  // ✅ Inicializar com dados mock para desenvolvimento
  const [collaborators, setCollaborators] = useState<Collaborator[]>([
    { 
      id: "col1", 
      name: "Joana Souza", 
      role: "Analista",
      superior: undefined,
      subordinados: [],
      projects: ["proj1"]
    },
    { 
      id: "col2", 
      name: "Carlos Silva", 
      role: "Dev",
      superior: "col1",
      subordinados: [],
      projects: ["proj1", "proj2"]
    },
  ]);
  
  const [projects, setProjects] = useState<Project[]>([
    { 
      id: "proj1", 
      name: "Projeto X", 
      status: "Ativo",
      link: "https://projeto-x.com",
      collaborators: ["col1", "col2"]
    },
    { 
      id: "proj2", 
      name: "Projeto Y", 
      status: "Concluído",
      link: "https://projeto-y.com",
      collaborators: ["col2"]
    },
  ]);
  
  const [teams, setTeams] = useState<Team[]>([
    { 
      id: "team1", 
      name: "Equipe Alpha",
      members: ["col1", "col2"]
    },
    { 
      id: "team2", 
      name: "Equipe Beta",
      members: []
    },
  ]);
  
  const [whiteboards, setWhiteboards] = useState<Whiteboard[]>([
    { 
      id: "wb1", 
      name: "Organograma RH",
      createdAt: new Date('2024-01-15'),
      updatedAt: new Date('2024-01-20'),
      nodes: [],
      edges: []
    },
    { 
      id: "wb2", 
      name: "Estrutura Projetos",
      createdAt: new Date('2024-02-01'),
      updatedAt: new Date('2024-02-05'),
      nodes: [],
      edges: []
    },
  ]);

  return (
    <WorkspaceContext.Provider 
      value={{ 
        selectedSection, 
        setSelectedSection, 
        sections,
        collaborators, 
        setCollaborators, 
        projects, 
        setProjects,
        teams,
        setTeams,
        whiteboards,
        setWhiteboards
      }}
    >
      {children}
    </WorkspaceContext.Provider>
  );
}

export function useWorkspace() {
  const ctx = useContext(WorkspaceContext);
  if (!ctx) throw new Error("useWorkspace must be used within WorkspaceProvider");
  return ctx;
}