import React from "react";
import { useWorkspace } from "../state/WorkspaceContext";
import { collaborators, projects, teams } from "../data/mockData";
import { CollaboratorCard } from "./cards/CollaboratorCard";
import { ProjectCard } from "./cards/ProjectCard";
import { TeamCard } from "./cards/TeamCard";
import { ProfileOverlay } from "./ProfileOverlay";
import { ProfileType } from "../types";
import { useProfile } from "../hooks/useProfile";

export function Gallery() {
  const { selectedSection } = useWorkspace();
  const { profile, openProfile, closeProfile } = useProfile();

  // Handler para abrir overlay
  const handleCardClick = (itemId: string, type: ProfileType) => {
    openProfile(itemId, type);
  };

  // Handler para navegação por teclado
  const handleKeyDown = (event: React.KeyboardEvent, itemId: string, type: ProfileType) => {
    if (event.key === "Enter" || event.key === " ") {
      event.preventDefault();
      handleCardClick(itemId, type);
    }
  };

  // Componente reutilizável para seções
  const renderSection = (title: string, items: any[], CardComponent: any, cardType?: ProfileType) => (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-6 text-gray-800">{title}</h1>
      
      {items.length === 0 ? (
        <div className="text-center py-8">
          <p className="text-gray-500 text-lg">
            Nenhum item encontrado em {title}
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {items.map((item) => {
            const hasInteraction = cardType && (cardType === "colaborador" || cardType === "projeto");
            
            return hasInteraction ? (
              <button
                key={item.id}
                onClick={() => handleCardClick(item.id, cardType)}
                onKeyDown={(e) => handleKeyDown(e, item.id, cardType)}
                className="cursor-pointer focus:outline-none focus:ring-2 focus:ring-blue-500 
                          rounded transition hover:shadow-lg hover:scale-105 text-left"
                aria-label={`Ver detalhes de ${item.name}`}
                style={{ background: "none", border: "none", padding: 0 }}
                tabIndex={0}
              >
                <CardComponent {...item} />
              </button>
            ) : (
              <div
                key={item.id}
                className="cursor-default"
                aria-label={`${title.slice(0, -1)} ${item.name}`}
              >
                <CardComponent {...item} />
              </div>
            );
          })}
        </div>
      )}
      
      {cardType && profile.type === cardType && profile.type !== "equipe" && (
        <ProfileOverlay
          entityId={profile.id}
          entityType={profile.type as "colaborador" | "projeto"}
          onClose={closeProfile}
        />
      )}
    </div>
  );

  // Renderizar baseado na seção selecionada
  switch (selectedSection) {
    case "Colaboradores":
      return renderSection("Colaboradores", collaborators, CollaboratorCard, "colaborador");
    
    case "Projetos":  
      return renderSection("Projetos", projects, ProjectCard, "projeto");
    
    case "Equipes":
      return renderSection("Equipes", teams, TeamCard);
    
    default:
      return null;
  }
}
